import React, {PureComponent} from 'react'
import {
    View, 
    Text, 
    TextInput,
    StyleSheet, 
    ScrollView, 
    TouchableOpacity, 
    ListView, 
    Image, 
    StatusBar,
    Button,
    Alert,
    NativeModules
} from 'react-native'

import { color } from '../../widget'
import { screen } from '../../common'
import { Title, Paragraph, Tip } from '../../widget/customText'
import PKButton from '../../widget/button'
import api from '../../common/api'
import Toast from '../../component/Toast'

type Props = {
    navigation: any,
}

type State = {
    Info: Object,
}

export default  class AddNewCarScene extends PureComponent<Props, State> {
    /**  */
    static navigationOptions = ({navigation}:any) => ({
        title: '',
        headerStyle: {backgroundColor: 'white'},
        headerTitleStyle:{
            color:'black',
            fontSize: 16
        },
    })
    constructor(props: Props) {
        super(props)
        this.state = {
            newPlate: null,
        }
    }

    submitAction = async () => {
        if (!this.state.newPlate || this.state.newPlate.length != 7) {
            Toast.show('')
            return;
        }
        var express = /^[A-Z]{1}[A-Z]{1}[A-Z0-9]{4}[A-Z0-9]{1}$/;
        result = express.test(this.state.newPlate);
        if (!result) {
            Toast.show('')
            return;
        }
        try {
            let params = {
                plate: this.state.newPlate,
                remark: ''
            }
            let response = await fetch(api.PLATE_API, {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(params),
            })
            let dataList = await response.json()
            console.log('ADD_PLATE_API:' + JSON.stringify(dataList));
            switch (response.status) {
                case 201:
                    {
                        Toast.show('')
                        if (this.props.navigation.state.params.callback) {
                            this.props.navigation.state.params.callback()
                            this.props.navigation.goBack()
                        }
                    }
                    break;
                case 400:
                    {
                        Toast.show('')
                    }
                    break;
                case 403:
                    {
                        Toast.show('')
                    }
                    break;
                default:
                    {
                        Toast.show('')
                    }
                    break;
            }
        } catch (error) {
            console.log(error)
            this.setState({refreshing: false})
        }
    }

    render = () => {
        return (
            <View style={styles.container}>
                <View style={styles.content}>
                    <Text></Text>
                    <TextInput 
                        placeholder=''
                        placeholderTextColor={color.gray}
                        value={this.state.account}
                        onChangeText={(text) => this.setState({newPlate:text})}
                        style={styles.plateInput}
                        underlineColorAndroid='transparent'
                    />
                </View>
                <View style={styles.submitButton}>
                    <PKButton
                        onPress={(this.submitAction)}
                        title=''
                        titleStyle={{color: '#fff',fontSize: 16,}}
                    />
                </View>
            </View>
        )
    }
}


const styles = StyleSheet.create({
    container: {
        flex: 1,
        flexDirection: 'column',
        backgroundColor: 'white',
    },
    content: {
        flexDirection: 'row',
        alignItems: 'center',
        top: 15,
        paddingLeft: 25,
        // borderTopWidth: screen.onePixel,
        borderBottomWidth: screen.onePixel,
        borderColor: color.border,
    },
    plateInput: {
        width: 150,
        height: 44,
        marginLeft: 15,
        paddingRight: 15,
    },
    submitButton: {
        width: screen.width - 50,
        height: 44,
        marginTop: 45,
        marginLeft: 25,
        marginBottom: 35,
        backgroundColor: color.primary,
        borderRadius: 5,
        justifyContent: 'center',
    }
})