import {AsyncStorage} from 'react-native'
import Storage from 'react-native-storage'
// import {sync} from './sync'

let storage = undefined
let defaultExpires = 1000 * 3600 * 24 * 365
let size = 1000

const createStorage = () => {

  storage = new Storage({
    // 1000
    size: size,

    // RNAsyncStoragewebwindow.localStorage
    // 
    storageBackend: AsyncStorage,

    // 1000 * 3600 * 24 null
    defaultExpires: defaultExpires,

    // 
    enableCache: true,

    // storage
    // sync
    // sync
    // sync
    // storage.sync
    // require
    // sync: sync
  })
}

const initStorage = () => {
  if (!storage) {
    createStorage()
  }
}

const _storage = {

  // key
  // 
  save(key, obj) {
    initStorage()
    storage.save({
      key: key,  // : key_!
      data: obj,
      // defaultExpires
      // null
      expires: defaultExpires
    })
  },

  // 
  load(key, callBack) {
    initStorage()
    storage.load({
      key: key,
      // autoSync(true)sync
      autoSync: false,
      // syncInBackground(true)
      // sync
      // falsesync()
      syncInBackground: true,
      // sync
      syncParams: {
        extraFetchOptions: { // 
        },
        someFlag: true,
      }
    }).then(ret => {
      // then
      // 
      // thenret
      // then
      // 
      // async/await
      callBack && callBack(ret)
      return ret
    }).catch(err => {
      //sync
      //catch
      switch (err.name) {
        case 'NotFoundError': {
          callBack({notFound: true})
        }
          // TODO
          break
        case 'ExpiredError':
          // TODO
          break
      }
    })
  },

  // keyid(key-id)
  getIdsForKey(id, callback) {
    initStorage()
    storage.getIdsForKey(id).then(ids => {
      callback && callback(ids)
    })
  },

  // key(key-id)
  getAllDataForKey(key, callback) {
    initStorage()
    storage.getAllDataForKey(key).then(users => {
      callback && callback(users)
    })
  },

  // !! key(key-id)
  clearMapForKey(key) {
    initStorage()
    storage.clearMapForKey(key)
  },

  // 
  remove(key) {
    initStorage()
    storage.remove({
      key: key
    })
  },

  // !! map"key-id"key
  clearMap() {
    initStorage()
    storage.clearMap()
  }
}

export {_storage as storage}