import React, {PureComponent} from 'react'
import { NativeModules } from 'react-native'

import {
    View,
    Text,
    TouchableOpacity,
    StyleSheet,
    ScrollView,
    Image,
    StatusBar,
    Platform,
    Linking,
    Alert
} from 'react-native';
import ScrollableTabView from 'react-native-scrollable-tab-view'
import DeviceInfo from 'react-native-device-info'

import HomeBookingScene from './HomeBookingScene'
import HomePaymentScene from './HomePaymentScene'

import color from '../../widget/color'
import {screen,system} from '../../common'
import api,{paramsFormatGET} from '../../common/api'
import Toast from '../../component/Toast'
import { storage } from '../../common/storage'

type Props = {
    navigation : any,
}

export default class HomeScene extends PureComponent<Props, State> {
    /**  */
    static navigationOptions = ({navigation}: any) => ({
        title: '',
        headerStyle: {backgroundColor: 'white'},
    })

    constructor(props: Props) {
        super(props);
        this.state = {
            dataList: [],
            refreshing: false,
        }
    }

    componentDidMount = () => {
        this.checkUpdate()
        setTimeout(() => {
            this.getDevicePushID()
        }, 5000);
    }

    getDevicePushID = async () => {
        let ParkingEventManager = NativeModules.ParkingEventManager
        ParkingEventManager.getDevicePushClientId((error, events) => {
            if (error) {
                console.error(error);
            } else {
                this.updateUserInfoForPushId(events)
                console.log('id' + JSON.stringify(events));
            }
        })
    }

    checkUpdate = async () => {
        try {
            let platform = system.isIOS?'IOS':'ANDROID';
            let params = {
                platform: platform,
            }
            console.log(':'+ paramsFormatGET(api.APP_UPDATE_API,params))
           let response = await fetch(paramsFormatGET(api.APP_UPDATE_API,params),{
                method: 'GET',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                },
            })
            let data = await response.json()
            console.log(':'+ paramsFormatGET(api.APP_UPDATE_API,params) + ':'+ JSON.stringify(data));
            switch (response.status) {
                case 200:
                {
                    let downloadUrl = data.download_url;
                    let new_version = data.new_version;
                    console.log(''+ DeviceInfo.getVersion() + '=====' + new_version)
                    if (new_version!=DeviceInfo.getVersion()) {
                        if (downloadUrl) {
                            Alert.alert('',(''), [
                                {text:"",},
                                {text:"", onPress:() => {
                                    Linking.openURL(downloadUrl)
                                }},
                            ])
                        }
                    }
                }
                    break;
                case 403:
                {
                    Toast.show('')
                }
                    break;
                default: 
                {
                    Toast.show('')
                }
                    break;
            }
        } catch (error) {
            console.log(error)
        }
    }


    updateUserInfoForPushId = async (deviceId) => {
        try {
            let params = {
                phone_type: 1,
                cid: deviceId,
            }
            console.log(JSON.stringify(params))
            let response = await fetch(api.GET_UPDATE_USER_INFO_API,{
                method: 'PUT',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(params),
            })
            let res = await response.json()
            console.log('GET_USER_INFO_API:'+ api.GET_UPDATE_USER_INFO_API + ':'+ JSON.stringify(res));
        } catch (error) {
            console.log(error)
        }
    }

    render() {
        let switchTitles = ['','']
        let types = ['','']
        return (
            <ScrollableTabView 
                style={styles.container}
                tabBarBackgroundColor='white'
                tabBarActiveTextColor='#EF5B30'
                tabBarInactiveTextColor='#555555'
                tabBarTextStyle={styles.tabBarText}
                tabBarUnderlineStyle={styles.tabBarUnderline} 
            >
                <HomeBookingScene
                    tabLabel={switchTitles[0]}
                    key={0}
                    types={types[0]}
                    navigation={(this.props.navigation)}
                ></HomeBookingScene>
                <HomePaymentScene
                    tabLabel={switchTitles[1]}
                    key={1}
                    types={types[1]}
                    navigation={(this.props.navigation)}
                ></HomePaymentScene>
            </ScrollableTabView>
        )
    }
}

const styles = StyleSheet.create ({
    container: {
        flex: 1,
        backgroundColor: color.paper,
    },
    tabBarText: {
        fontSize: 14,
        marginTop: 13,
    },
    tabBarUnderline: {
        backgroundColor: color.primary
    },
    navTitle: {
        width: screen.width * 0.7,
        height: 30,
        borderRadius: 19,
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: 'red',
        alignSelf: 'center',
    },
})


